



































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import service from "@/service/index";
interface Add {
  form: Object;
  formLabelWidth: String;
  dialogFormVisible: Boolean;
}
@Component
export default class AddAdmin extends Vue implements Add {
  @Prop() private visible!: Boolean;
  form = {
    name: "",
    sex: "",
    addr: "",
    email: "",
    buildtime: ""
  };
  formLabelWidth = "120px";
  options = [
    {
      value: "北京",
      label: "北京",
      children: [
        {
          value: "朝阳区",
          label: "朝阳区"
        },
        {
          value: "海淀区",
          label: "海淀区"
        },
        {
          value: "东城区",
          label: "东城区"
        },
        {
          value: "西城区",
          label: "西城区"
        },
        {
          value: "其他",
          label: "其他"
        }
      ]
    },
    {
      value: "其他地区",
      label: "其他地区"
    }
  ];
  // 通过计算属性获取父级组件传值，并进行form表单清空
  get dialogFormVisible() {
    if (this.visible) {
      for (let key in this.form) {
        this.form[key] = "";
      }
    }
    return this.visible;
  }
  set dialogFormVisible(val) {
    !val && this.$emit("update:visible", false); // 更新父组件状态，注意.sync修饰符
  }
  public addAdmin() {
    service.postAdminList(this.form).then(res => {
      this.dialogFormVisible = false;
      let { code, msg } = res.data;
      if (code == 0) {
        this.$message({
          message: msg,
          type: "success"
        });
      }
      this.$emit("getAdmin");
    });
  }
}
