































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import EditAdmin from "./EditAdmin/EditAdmin.vue";
import service from "../../../service";
@Component({
  components: { EditAdmin }
})
export default class AdminContent extends Vue {
  @Prop() tableData!: Array<object>;
  @Prop() listLoading!: Boolean;

  row = {};
  editVisible = false;
  tableHeader: Array<object> = [
    {
      label: "ID",
      align: "center",
      prop: "id",
      width: "150",
      sortable: "custom"
    },
    {
      label: "名字",
      align: "center",
      prop: "name",
      width: "150",
      sortable: ""
    },
    { label: "性别", align: "center", prop: "sex", width: "150", sortable: "" },
    {
      label: "地址",
      align: "center",
      prop: "addr",
      width: "150",
      sortable: ""
    },
    {
      label: "email",
      align: "center",
      prop: "email",
      width: "300",
      sortable: ""
    }
  ];

  handleEdit(index, row) {
    this.editVisible = true;
    this.row = Object.assign({}, row); // 该处要对对象进行浅拷贝
  }

  handleDel(index, row) {
    let params = { id: row.id };
    service.deleteAdminList(params).then(res => {
      console.log(res, ":Res");
      let { code, msg } = res;
      this.$message({
        message: msg,
        type: "success"
      });
      this.$emit("contentFlush");
    });
  }

  //排序
  sortChange(column) {
    let { prop, order } = column;
    if (order === "descending") {
      this.tableData.sort((a, b) => b["id"] - a["id"]);
    } else {
      this.tableData.sort((a, b) => a["id"] - b["id"]);
    }
  }

  // 当修改完成需要刷新数据（会返回最顶层）
  public flushList() {
    this.$emit("contentFlush");
  }
}
