









import AdminHead from "./components/AdminHead.vue";
import AdminContent from "./components/AdminContent.vue";
import AdminFoot from "./components/AdminFoot.vue";
import { Vue, Component } from "vue-property-decorator";
@Component({
  components: {
    AdminHead,
    AdminContent,
    AdminFoot
  }
})
export default class AdminTable extends Vue {
  page: number = 1;
  tableData: [] = [];
  total: number = 1;
  listLoading: boolean = true;

  // 接收从Head传来的数据（所有交互的数据都会传回父级，在由父级下发）
  getHeadData(data, listLoading = false) {
    console.log(data, ":Data");
    let result = data.result;
    this.tableData = result.admins;
    this.total = result.total;
    this.listLoading = false;
    if (this.total < 15) {
      this.page = 1;
    }
  }

  //跳用head的方法
  contentFlush() {
    this.$nextTick(() => {
      //@ts-ignore
      this.$refs.child.getAdmin();
    });
  }

  getFootData(page, pageSize) {
    this.page = page;
    this.$nextTick(() => {
      //@ts-ignore
      this.$refs.child.getAdmin();
    });
  }
}
